/* Add this CSS to your stylesheet or component */

/* Style the "Previous" and "Next" buttons */
.pagination{
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
}
.pagination .previous,
.pagination .next {
  background-color: #007bff; /* Change to your desired background color */
  color: #fff; /* Change to your desired text color */
  padding: 8px 16px; /* Adjust padding as needed */
  border: none;
  border-radius: 4px; /* Adjust the border radius as needed */
  cursor: pointer;
}

/* Style the "Previous" and "Next" buttons when they are disabled */
.pagination .previous.disabled,
.pagination .next.disabled {
  background-color: #ccc; /* Change to your desired background color for disabled buttons */
  color: #888; /* Change to your desired text color for disabled buttons */
  cursor: not-allowed;
}

/* Style the page number buttons */
.pagination .number {
  display: inline-block; /* Ensure the buttons are displayed horizontally */
  margin: 4px; /* Adjust margin spacing between buttons */
  padding: 6px 12px; /* Adjust padding as needed */
  background-color: #007bff; /* Change to your desired background color */
  color: #fff; /* Change to your desired text color */
  border-radius: 4px; /* Adjust the border radius as needed */
  cursor: pointer;
}

/* Style the active page number button */
.pagination .active {
  background-color: #555; /* Change to your desired background color for the active button */
}

/* Style the disabled page number button */
.pagination ul li.disabled {
  background-color: #ccc; /* Change to your desired background color for disabled buttons */
  color: #888; /* Change to your desired text color for disabled buttons */
  cursor: not-allowed;
}
